<!-- 내가 기존에 jsp나 html 파일을 만들어서 삽입하던 것을 각각의 vue를 만들어서 삽입하자는 것 -->
<template>
    <div v-if="currentTutorial" class="edit-form">
        <h4>Tutorials</h4>
        <form>
            <div class="form-group">
                <label for="title">Title</label>
                <input type="text" class="form-control" id="title" required v-model="currentTutorial.title"
                    name="title" />
            </div>
            <div class="form-group">
                <label for="description">description</label>
                <input type="text" class="form-control" id="description" required v-model="currentTutorial.description"
                    name="description" />
            </div>
            <div class="form-group">
                <label><strong>Status:</strong></label>
                {{ currentTutorial.published ? "published" : "Pending" }}
            </div>
        </form>
        <button class="badge badge-primary mr-2" @click.prevent="updatePublish(currentTutorial.published)">
            {{ currentTutorial.published ? "Unpublish" : "Publish" }}</button>
        <button class="badge badge-danger mr-2" @click="deleteTutorial">
            Delete
        </button>

        <button type="submit" class="badge badge-success" @click="updateTutorial">
            Update
        </button>
        <p>{{ message }}</p>

    </div>
    <div v-else>
        <br />
        <p>Please click on a Tutorial...</p>

    </div>
</template>

<script>
import TutorialDataService from "../services/TutorialDataService.js";

export default {
    name: "tutorial",
    data() {
        return {
            currentTutorial: null
            , message: ""
        };
    },
    methods: {
        getTutorial(id) {
            TutorialDataService.get(id)
                .then(res => {
                    this.currentTutorial = res.data;
                    console.log(res.data)
                })
                .catch(e => {
                    console.log(e)
                })
        },
        updatePublish(status) {
            var datas = {
                id: this.currentTutorial.id,
                title: this.currentTutorial.title,
                description: this.currentTutorial.description,
                published: !status
            };
            TutorialDataService.update(this.currentTutorial.id, datas)
                .then(res => {
                    this.currentTutorial.published = !status;
                    console.log(res.data)
                })
                .catch(e => {
                    console.log(e)
                })
        },
        updateTutorial() {
            TutorialDataService.update(this.currentTutorial.id, this.currentTutorial)
                .then(res => {
                    this.message = "success"
                    console.log(res.data)
                })
                .catch(e => {
                    console.log(e)
                })
        },
        deleteTutorial() {
            TutorialDataService.delete(this.currentTutorial.id)
                .then(res => {
                    this.$router.push({ name: "tutorials" })//name:"" 경로로 이동하라.
                })
                .catch(e => {
                    console.log(e)
                })
        }
    },
    mounted() {
        this.message = '';
        this.getTutorial(this.$route.params.id);
    }

};
</script>
<style>
.edit-form {
  max-width: 300px;
  margin: auto;
}
</style>